@font-face{font-family:'spotify-sansserif';src:font_url("/assets/spotify-regular.woff") format("woff");font-weight:normal;font-style:normal}@font-face{font-family:'spotify-thin';src:font_url("/assets/spotify-thin.woff") format("woff");font-weight:normal;font-style:normal}*{z-index:0}body{background-color:#fbc3db}#gradientBorder{--border-width: 3px;position:absolute;display:flex;justify-content:center;align-items:center;width:300px;height:200px;background:#222;border-radius:var(--border-width);top:40%;left:8%}#gradientBorder::after{position:absolute;content:"";top:calc(-1 * var(--border-width));left:calc(-1 * var(--border-width));z-index:-1;width:calc(100% + var(--border-width) * 2);height:calc(100% + var(--border-width) * 2);background:linear-gradient(60deg, #5f86f2, #a65ff2, #f25fd0, #f25f61, #f2cb5f, #abf25f, #5ff281, #5ff2f0);background-size:300% 300%;background-position:0 50%;border-radius:calc(2 * var(--border-width));-webkit-animation:moveGradient 4s alternate infinite;animation:moveGradient 4s alternate infinite}@-webkit-keyframes moveGradient{50%{background-position:100% 50%}}@keyframes moveGradient{50%{background-position:100% 50%}}#modal{display:block;position:absolute;z-index:1;width:100%;height:100%;top:0px;left:0px;float:left;background-color:#000;background-color:rgba(0,0,0,0.4)}#modalContent{background-color:#7a7a78;position:relative;margin:auto;padding:20px;border-radius:3px;width:20rem;color:white;background:linear-gradient(90deg, #7a7a78 4px, transparent 1%) center,linear-gradient(#7a7a78 4px, transparent 1%) center,#a799cc;background-size:5px 5px}#nav{display:flex;justify-content:flex-start;border-bottom:2px, solid, #483e73}h1{font-family:'spotify-sansserif', Arial, sans-serif;-webkit-text-stroke-width:2px;-webkit-text-stroke-color:#483e73;font-size:60px;color:#fbc3db;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;margin:10px;margin-left:30px}#info{display:flex;flex-direction:column;height:-webkit-min-content;height:-moz-min-content;height:min-content;width:-webkit-min-content;width:-moz-min-content;width:min-content}#data-top{display:flex;justify-content:center}#main{display:flex;justify-content:left;width:-webkit-min-content;width:-moz-min-content;width:min-content;margin-left:35px}#albumart{width:15rem}#artist-info{font-family:'spotify-thin', Arial, sans-serif}#data-container{float:left;height:-webkit-min-content;height:-moz-min-content;height:min-content;width:-webkit-min-content;width:-moz-min-content;width:min-content;position:relative;top:-100px}#genre-cloud>div{display:flex;flex-direction:column;font-family:'spotify-thin', Arial, sans-serif;border-radius:8px;background-color:#483e73;color:whitesmoke;padding:5px;margin:5px;opacity:66%;white-space:nowrap;background-color:#483e73;background:linear-gradient(90deg, #483e73 4px, transparent 1%) center,linear-gradient(#483e73 4px, transparent 1%) center,#a799cc;background-size:5px 5px}#data-bars{font-family:'spotify-thin', Arial, sans-serif;border-radius:8px;color:whitesmoke;opacity:66%;height:-webkit-min-content;height:-moz-min-content;height:min-content;white-space:nowrap;padding:5px;background:linear-gradient(90deg, #483e73 4px, transparent 1%) center,linear-gradient(#483e73 4px, transparent 1%) center,#a799cc;background-size:5px 5px}svg{transform:scale(0.8, 0.8)}
